import React from "react";
import { fade, makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import ButtonBase from "@material-ui/core/ButtonBase";
import Util from "../Util";
const MasterProduct =
  "https://mrroin.github.io/Master_Ultracolor/static/media/MasterProduct.jpg";
const PlascticProduct =
  "https://mrroin.github.io/Master_Ultracolor/static/media/PlascticProduct.jpg";
const EqualizationProduct =
  "https://mrroin.github.io/Master_Ultracolor/static/media/EqualizationProduct.jpg";
const PigmentProduct =
  "https://mrroin.github.io/Master_Ultracolor/static/media/PigmentProduct.jpg";
const OutroProduct =
  "https://mrroin.github.io/Master_Ultracolor/static/media/OutroProduct.jpg";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: theme.spacing(8),
    marginBottom: theme.spacing(4),
  },
  images: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexWrap: "wrap",
  },
  imageWrapper: {
    position: "relative",
    display: "block",
    padding: 0,
    borderRadius: 0,
    height: "40vh",
    [theme.breakpoints.down("sm")]: {
      width: "100% !important",
      height: 100,
    },
    "&:hover": {
      zIndex: 1,
    },
    "&:hover $imageBackdrop": {
      opacity: 0.15,
    },
    "&:hover $imageMarked": {
      opacity: 0,
    },
    "&:hover $imageTitle": {
      border: "4px solid currentColor",
    },
  },
  imageButton: {
    position: "absolute",
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    color: theme.palette.common.white,
  },
  imageSrc: {
    position: "absolute",
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundSize: "cover",
    backgroundPosition: "center 40%",
  },
  imageBackdrop: {
    position: "absolute",
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    background: theme.palette.common.black,
    opacity: 0.5,
    transition: theme.transitions.create("opacity"),
  },
  imageTitle: {
    position: "relative",
    padding: `${theme.spacing(2)}px ${theme.spacing(4)}px 14px`,
  },
  imageMarked: {
    height: 3,
    width: 18,
    background: theme.palette.common.white,
    position: "absolute",
    bottom: -2,
    left: "calc(50% - 9px)",
    transition: theme.transitions.create("opacity"),
  },
}));

const ProductCategories = (props: any): any => {
  const { goPage } = props;
  const classes = useStyles();

  const images = [
    {
      url: PlascticProduct,
      title: "categories.plastic",
      width: "60%",
      path: "/Master_Ultracolor/plastic",
    },
    {
      url: MasterProduct,
      title: "categories.master",
      width: "40%",
      path: "/Master_Ultracolor/masterbatch",
    },
    {
      url: PigmentProduct,
      title: "categories.pigments",
      width: "28%",
      path: "/Master_Ultracolor/pigments",
    },
    {
      url: EqualizationProduct,
      title: "categories.equalization",
      width: "45%",
      path: "/Master_Ultracolor/equalization",
    },
    {
      url: OutroProduct,
      title: "categories.outro",
      width: "27%",
      path: "/Master_Ultracolor/other",
    },
  ];
  return (
    <Container className={classes.root} component="section">
      <Typography variant="h4" align="center" component="h2">
        {Util.traducedMessage(
          props.messages[props.language],
          "muc.app.categories.title",
        )}
      </Typography>
      <div className={classes.images}>
        {images.map((image) => (
          <ButtonBase
            key={image.title}
            className={classes.imageWrapper}
            style={{
              width: image.width,
            }}
            onClick={() => goPage(image.path)}
          >
            <div
              className={classes.imageSrc}
              style={{
                backgroundImage: `url(${image.url})`,
              }}
            />
            <div className={classes.imageBackdrop} />
            <div className={classes.imageButton}>
              <Typography
                component="h3"
                variant="h6"
                color="inherit"
                className={classes.imageTitle}
              >
                {Util.traducedMessage(
                  props.messages[props.language],
                  image.title,
                )}
                <div className={classes.imageMarked} />
              </Typography>
            </div>
          </ButtonBase>
        ))}
      </div>
    </Container>
  );
};

export default ProductCategories;
