export enum UtilActionTypes {
  ACTION = "action-util",
  THEME = "theme",
  LANGUAGE = "language",
  AGENT = "loginResponse",
  GO_PAGE = "goPage",
  BACK_PAGE = "backPage",
  LOADING = "loading",
  MESSAGES = "messages",
}

interface IGoPageAction {
  type: UtilActionTypes.GO_PAGE;
  payload: { path: string };
}

interface IBackPageAction {
  type: UtilActionTypes.BACK_PAGE;
}

interface IUtilAction {
  type: UtilActionTypes.ACTION;
}

interface IThemeAction {
  type: UtilActionTypes.THEME;
  payload: { theme: any };
}

interface ILanguageAction {
  type: UtilActionTypes.LANGUAGE;
  payload: { language: any };
}

interface IAgentAction {
  type: UtilActionTypes.AGENT;
  payload: { agent: any };
}

interface ILoadingAction {
  type: UtilActionTypes.LOADING;
  payload: { loading: any };
}

interface IMessagesAction {
  type: UtilActionTypes.MESSAGES;
  payload: { messagesApp: any };
}

export function goPageAction(path: string): IGoPageAction {
  return {
    type: UtilActionTypes.GO_PAGE,
    payload: { path: path },
  };
}

export function backPageAction(): IBackPageAction {
  return {
    type: UtilActionTypes.BACK_PAGE,
  };
}

export function themeAction(data: any): IThemeAction {
  return {
    type: UtilActionTypes.THEME,
    payload: { theme: data },
  };
}

export function languageAction(data: any): ILanguageAction {
  return {
    type: UtilActionTypes.LANGUAGE,
    payload: { language: data },
  };
}

export function agentAction(data: any): IAgentAction {
  return {
    type: UtilActionTypes.AGENT,
    payload: { agent: data },
  };
}

export function loadingAction(data: any): ILoadingAction {
  return {
    type: UtilActionTypes.LOADING,
    payload: { loading: data },
  };
}

export function messagesAction(data: any): IMessagesAction {
  return {
    type: UtilActionTypes.MESSAGES,
    payload: { messagesApp: data },
  };
}

export type UtilAction =
  | IUtilAction
  | IThemeAction
  | ILanguageAction
  | IAgentAction
  | IGoPageAction
  | IBackPageAction
  | ILoadingAction
  | IMessagesAction;
