import { combineReducers } from "redux";
import { connectRouter } from "connected-react-router";
//ADD o QUIT if route is f7 or react router
import utilReducer, { initialUtilState } from "./util/reducer";
import { IUtilState } from "./util/reducer";
import { RouterState, RouterActionType } from "connected-react-router";

export interface IState {
  util: IUtilState;
  // router: RouterState;
}

export const initialState: IState = {
  util: initialUtilState,
};

export default (history: any) =>
  combineReducers({
    router: connectRouter(history),
    util: utilReducer,
  });
