import React from "react";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Link from "@material-ui/core/Link";
import Container from "@material-ui/core/Container";
import TextField from "../../atoms/TextField";
import Util from "../Util";
const appFooterFacebook =
  "https://mrroin.github.io/Master_Ultracolor/static/media/appFooterFacebook.png";
const appFooterTwitter =
  "https://mrroin.github.io/Master_Ultracolor/static/media/appFooterTwitter.png";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    backgroundColor: theme.palette.secondary.light,
  },
  container: {
    marginTop: theme.spacing(8),
    marginBottom: theme.spacing(8),
    display: "flex",
  },
  iconsWrapper: {
    height: 120,
  },
  icons: {
    display: "flex",
  },
  icon: {
    width: 48,
    height: 48,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: theme.palette.primary.main,
    marginRight: theme.spacing(1),
    "&:hover": {
      backgroundColor: theme.palette.secondary.main,
    },
  },
  list: {
    margin: 0,
    listStyle: "none",
    padding: 0,
  },
  listItem: {
    paddingTop: theme.spacing(0.5),
    paddingBottom: theme.spacing(0.5),
  },
  language: {
    marginTop: theme.spacing(1),
    width: 150,
    borderColor: "red",
  },
}));

function Copyright(props: any) {
  return (
    <React.Fragment>
      {"© "}
      <Link color="inherit" href="https://material-ui.com/">
        {props.name}
      </Link>{" "}
      {new Date().getFullYear()}
    </React.Fragment>
  );
}

const LANGUAGES = [
  {
    code: "es",
    name: "muc.language.es",
  },
  {
    code: "en",
    name: "muc.language.en",
  },
  {
    code: "fr",
    name: "muc.language.fr",
  },
];

const AppFooter = (props: any): any => {
  const classes = useStyles();

  return (
    <Typography component="footer" className={classes.root}>
      <Container className={classes.container}>
        <Grid container spacing={5}>
          <Grid item xs={6} sm={4} md={3}>
            <Grid
              container
              direction="column"
              justify="flex-end"
              className={classes.iconsWrapper}
              spacing={2}
            >
              <Grid item className={classes.icons}>
                <a href="https://material-ui.com/" className={classes.icon}>
                  <img src={appFooterFacebook} alt="Facebook" />
                </a>
                <a
                  href="https://twitter.com/MaterialUI"
                  className={classes.icon}
                >
                  <img src={appFooterTwitter} alt="Twitter" />
                </a>
              </Grid>
              <Grid item>
                <Copyright
                  name={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.name",
                  )}
                />
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={6} sm={4} md={2}>
            <Typography variant="h6" gutterBottom>
              {Util.traducedMessage(
                props.messages[props.language],
                "muc.app.footer.legal",
              )}
            </Typography>
            <ul className={classes.list}>
              <li className={classes.listItem}>
                <Link href="/premium-themes/onepirate/terms/">
                  {Util.traducedMessage(
                    props.messages[props.language],
                    "muc.app.footer.terms.privacy",
                  )}
                </Link>
              </li>
            </ul>
          </Grid>
          <Grid item xs={6} sm={8} md={4}>
            <Typography variant="h6" gutterBottom>
              {Util.traducedMessage(
                props.messages[props.language],
                "muc.app.footer.language",
              )}
            </Typography>
            <TextField
              select
              SelectProps={{
                native: true,
              }}
              className={classes.language}
              onChange={(e: any) => props.changeLanguage(e.target.value)}
            >
              {LANGUAGES.map((language) => (
                <option
                  value={language.code}
                  key={language.code}
                  selected={language.code === props.language}
                >
                  {Util.traducedMessage(
                    props.messages[props.language],
                    language.name,
                  )}
                </option>
              ))}
            </TextField>
          </Grid>
        </Grid>
      </Container>
    </Typography>
  );
};

export default AppFooter;
