import React from "react";
import { fade, makeStyles } from "@material-ui/core/styles";
import WelcomeLayout from "../WelcomeLayout";
import Typography from "@material-ui/core/Typography";
import Util from "../Util";
const Pigment =
  "https://mrroin.github.io/Master_Ultracolor/static/media/Pigment.jpg";

const useStyles = makeStyles((theme) => ({
  background: {
    backgroundImage: `url(${Pigment})`,
    // backgroundColor: "#7fc7d9", // Average color of the background image.
    backgroundPosition: "center",
  },
  button: {
    minWidth: 200,
  },
  h5: {
    marginBottom: theme.spacing(4),
    marginTop: theme.spacing(4),
    [theme.breakpoints.up("sm")]: {
      marginTop: theme.spacing(10),
    },
  },
  more: {
    marginTop: theme.spacing(2),
  },
}));

const Welcome = (props: any): any => {
  const { backgroundClassName, children } = props;
  const classes = useStyles();
  return (
    <WelcomeLayout backgroundClassName={classes.background}>
      <img style={{ display: "none" }} src={Pigment} alt="increase priority" />
      <Typography color="inherit" align="center" variant="h2">
        {Util.traducedMessage(
          props.messages[props.language],
          "muc.app.welcome.title",
        )}
      </Typography>
      <Typography
        color="inherit"
        align="center"
        variant="h5"
        className={classes.h5}
      >
        {Util.traducedMessage(
          props.messages[props.language],
          "muc.app.welcome.subtitle",
        )}
      </Typography>
      <Typography variant="body2" color="inherit" className={classes.more}>
        {Util.traducedMessage(
          props.messages[props.language],
          "muc.app.welcome.message",
        )}
      </Typography>
    </WelcomeLayout>
  );
};

export default Welcome;
