import { combineEpics, createEpicMiddleware } from "redux-observable";
import utilEpics from "./util/epics";
import { Action } from "redux";
import { IState } from "./reducer";
import { ApiServicesFactory } from "./factory/ApiServicesFactory";

export const rootEpic = combineEpics(utilEpics);

const servicesApi = new ApiServicesFactory().create();

export default createEpicMiddleware<Action, Action, IState>({
  dependencies: { serviceApi: servicesApi },
});
