import React, { useState } from "react";
import { fade, makeStyles } from "@material-ui/core/styles";
import * as Yup from "yup";
import { Formik, Form, Field } from "formik";
import Icon from "@material-ui/core/Icon";
import { AddCircle, Delete, Camera as IconCamera } from "@material-ui/icons";
import { v4 as uuidv4 } from "uuid";
import Camera from "../Camera";
import {
  Button,
  LinearProgress,
  MenuItem,
  FormControl,
  InputLabel,
  FormControlLabel,
  Typography,
  Container,
} from "@material-ui/core";
import MuiTextField from "@material-ui/core/TextField";
import {
  fieldToTextField,
  TextField,
  TextFieldProps,
  Select,
  Switch,
} from "formik-material-ui";
import { ColorPicker } from "../ColorPicker";
import styled from "styled-components";
import Util from "../Util";

const Photo = styled.img`
  width: 12vw;
  margin: 1vw;
`;

const useStyles = makeStyles((theme) => ({
  muiappbar: {
    color: theme.palette.common.white,
  },
  container: {
    [theme.breakpoints.between("xs", "sm")]: {
      width: "90% !important",
      "& img": {
        width: "65vw",
      },
    },
    [theme.breakpoints.between("sm", "md")]: {
      width: "50% !important",
      "& img": {
        width: "55vw",
      },
    },
    [theme.breakpoints.between("md", "lg")]: {
      width: "40% !important",
      "& img": {
        width: "33vw",
      },
    },
    [theme.breakpoints.between("lg", "xl")]: {
      width: "40% !important",
      "& img": {
        width: "33vw",
      },
    },
    "& .Mui-disabled": {
      color: "rgb(32 31 31 / 30%) !important",
      backgroundColor: "rgb(168 165 165 / 41%) !important",
    },
    "& input": {
      borderBottom: "0.5px solid " + theme.palette.primary.main,
    },
  },
  formContainer: {
    marginTop: "2vh",
    marginBottom: "2vh",
    textAlign: "right",
  },
  field: {
    // "& div": {
    //   "& div": {
    //     borderBottom: "2px solid " + theme.palette.primary.main,
    //   },
    // },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
    [theme.breakpoints.down("xl")]: {
      width: "100%",
    },
    "& input": {
      color: theme.palette.primary.main,
    },
    "& textarea": {
      color: theme.palette.primary.main,
    },
    "& .MuiFormHelperText-root": {
      color: theme.palette.primary.main,
    },
    "& .MuiSelect-select": {
      color: theme.palette.primary.main,
    },
  },
  imageSrc: {
    position: "absolute",
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundSize: "cover",
    backgroundPosition: "center 40%",
  },
}));

const SearchAppBar = (props: any): any => {
  const classes = useStyles();
  const [showCamera, setShowCamera] = useState(false);
  const [colorsInput, setColorsInput] = useState<any[]>([]);
  const [photos, setPhotos] = useState<any[]>([]);
  const handleSetPhotos = (photo: string) => {
    setShowCamera(false);
    console.log(photo);
    photos.push(photo);
    setPhotos(photos);
  };
  const handleShowCamera = () => {
    if (photos.length < 10) {
      setShowCamera(true);
    }
  };
  const handleAddColor = () => {
    if (colorsInput.length <= 8) {
      const index = colorsInput.length ? colorsInput.length + 1 : 1;
      setColorsInput([
        ...colorsInput,
        {
          index: index + 1,
          key: uuidv4(),
          id: "color" + (index + 1),
          color: "black",
        },
      ]);
    }
  };
  const handlRemoveColor = (key: string) => {
    setColorsInput([]);
    let index = false;
    const colors: any = [];
    for (let i = 0; i < colorsInput.length; i++) {
      if (colorsInput[i].key !== key) {
        colors.push({
          ...colorsInput[i],
          key: uuidv4(),
          index: index ? i + 2 - 1 : i + 2,
          id: "color" + (index ? i + 2 - 1 : i + 2),
        });
      } else {
        index = true;
      }
    }

    setColorsInput(colors);
  };
  const schema = Yup.object().shape({
    type: Yup.string().required("Requerido"),
    name: Yup.string().required("Requerido"),
    description: Yup.string().required("Requerido"),
    height: Yup.string().required("Requerido"),
    width: Yup.string().required("Requerido"),
    deep: Yup.string().required("Requerido"),
    capacity: Yup.string().required("Requerido"),
    price: Yup.string().required("Requerido"),
    priceWholesale: Yup.string().required("Requerido"),
    priceInstructions: Yup.string().required("Requerido"),
    pieces: Yup.string().required("Requerido"),
  });
  console.log(JSON.stringify(colorsInput));
  return (
    <Formik
      initialValues={{}}
      onSubmit={(values) => {
        console.log(JSON.stringify(values));
        props.onHandleSubmit(values);
      }}
      validationSchema={schema}
      isInitialValid={schema.isValidSync({})}
    >
      {({ isValid, values, setValues, handleSubmit }) => {
        return (
          <Container className={classes.formContainer}>
            <Form onSubmit={handleSubmit}>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  type="type"
                  name="type"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.type.product",
                  )}
                  select
                  variant="standard"
                  margin="normal"
                  InputLabelProps={{
                    shrink: true,
                  }}
                >
                  {props.typeProducts.map((option: any) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Field>
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="name"
                  type="text"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.name",
                  )}
                />
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="description"
                  type="text"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.description",
                  )}
                  InputProps={{
                    multiline: true,
                    rows: 2,
                    rowsMax: 4,
                  }}
                />
              </Container>
              <Container className={classes.container}>
                <ColorPicker
                  currentColor={"black"}
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.color",
                  )}
                  onChange={(color: any) => {
                    setValues({
                      ...values,
                      colorMain: color,
                    });
                  }}
                  moreIcon={
                    <Icon>
                      <AddCircle onClick={() => handleAddColor()} />
                    </Icon>
                  }
                />
                <>
                  {colorsInput.map((item: any) => {
                    return (
                      <ColorPicker
                        key={item.key}
                        id={item.id}
                        currentColor={item.color}
                        label={
                          Util.traducedMessage(
                            props.messages[props.language],
                            "muc.add.product.color",
                          ) + item.index
                        }
                        onChange={(color: any, key: any) => {
                          const colors: any = [];
                          for (let i = 0; i < colorsInput.length; i++) {
                            if (colorsInput[i].id !== key) {
                              colors.push({
                                ...colorsInput[i],
                              });
                            } else {
                              colors.push({
                                ...colorsInput[i],
                                color: color,
                              });
                            }
                          }
                          setColorsInput(colors);
                        }}
                        moreIcon={
                          <Icon>
                            <Delete
                              onClick={() => handlRemoveColor(item.key)}
                            />
                          </Icon>
                        }
                      />
                    );
                  })}
                </>
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="height"
                  type="number"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.height",
                  )}
                />
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="width"
                  type="number"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.width",
                  )}
                />
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="deep"
                  type="number"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.deep",
                  )}
                />
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="capacity"
                  type="number"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.capacity",
                  )}
                />
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  type="typeCapacity"
                  name="typeCapacity"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.typeCapacity",
                  )}
                  select
                  variant="standard"
                  margin="normal"
                  InputLabelProps={{
                    shrink: true,
                  }}
                >
                  {props.typeCapacity.map((option: any) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Field>
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="price"
                  type="number"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.price",
                  )}
                />
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="priceWholesale"
                  type="number"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.priceWholesale",
                  )}
                />
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="priceInstructions"
                  type="number"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.priceInstructions",
                  )}
                />
              </Container>
              <Container className={classes.container}>
                <Field
                  className={classes.field}
                  component={TextField}
                  name="pieces"
                  type="number"
                  label={Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.pieces",
                  )}
                />
              </Container>
              <Container
                className={classes.container}
                style={{
                  textAlign: "left",
                  paddingTop: "2vh",
                }}
              >
                <label>
                  {Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.photo",
                  )}
                </label>
                &nbsp;&nbsp;&nbsp;
                <Icon>
                  <IconCamera onClick={() => handleShowCamera()} />
                </Icon>
              </Container>
              <Container
                className={classes.container}
                style={{
                  textAlign: "left",
                }}
              >
                <>
                  {photos.map((photo: any, index: number) => {
                    console.log(index);
                    return (
                      <>
                        <Icon>
                          <Delete
                            onClick={() => {
                              const newPhotos = photos.filter(
                                (p) => p !== photo,
                              );
                              setPhotos(newPhotos);
                            }}
                          />
                        </Icon>
                        <Photo src={photo} />
                      </>
                    );
                  })}
                </>
              </Container>
              <>
                {showCamera ? (
                  <Camera handleSetPhotos={handleSetPhotos} />
                ) : null}
              </>
              <Container className={classes.container}>
                <Button color="primary" type="submit" disabled={!isValid}>
                  {Util.traducedMessage(
                    props.messages[props.language],
                    "muc.add.product.save",
                  )}
                </Button>
              </Container>
            </Form>
          </Container>
        );
      }}
    </Formik>
  );
};

export default SearchAppBar;
