import { IState, initialState } from "../reducer";
export type IGetThemeSelector = (state: IState) => any;
export type IGetAgentSelector = (state: IState) => any;
export type IGetLanguageSelector = (state: IState) => any;
export type IGetLoadingSelector = (state: IState) => any;
export type IGetMessagesAppSelector = (state: IState) => any;

const languageSelector = (state: IState = initialState) => {
  return state.util["language"] || undefined;
};

export const getLanguageSelector: IGetLanguageSelector = (state: IState) =>
  languageSelector(state);

const agentSelector = (state: IState = initialState) => {
  return state.util["agent"] || undefined;
};

export const getAgentSelector: IGetAgentSelector = (state: IState) =>
  agentSelector(state);

const themeSelector = (state: IState = initialState) => {
  return state.util["theme"] || undefined;
};

export const getThemeSelector: IGetThemeSelector = (state: IState) =>
  themeSelector(state);

const loadingSelector = (state: IState = initialState) => {
  return state.util["loading"] || undefined;
};

export const getLoadingSelector: IGetLoadingSelector = (state: IState) =>
  loadingSelector(state);

const messageAppSelector = (state: IState = initialState) => {
  return state.util["messagesApp"] || undefined;
};

export const getMessageAppSelector: IGetMessagesAppSelector = (state: IState) =>
  messageAppSelector(state);
