/* eslint-disable @typescript-eslint/no-this-alias */
import React, { Component } from "react";
import { bindActionCreators } from "redux";
import _ from "lodash";
import { connect } from "react-redux";
import Welcome from "../../organisms/Welcome";
import Services from "../../organisms/Services";
import ProductCategories from "../../organisms/ProductCategories";
import EqualizationHowItWorks from "../../organisms/EqualizationHowItWorks";
import LinkContact from "../../organisms/LinkContact";
import { goPageAction } from "../../../store/util/actions";
import {
  getMessageAppSelector,
  getLanguageSelector,
} from "../../../store/util/selectors";

interface IProps {
  dispatch: any;
  theme: any;
  goPage: any;
  messages: any;
  language: any;
}

interface IState {}

class HomeContainer extends Component<IProps, IState> {
  constructor(props: IProps | Readonly<IProps>) {
    super(props);
    this.state = {};
    console.log("HomeContainer");
  }

  render() {
    const { goPage, messages, language } = this.props;
    return (
      <>
        <Welcome messages={messages ? messages : {}} language={language} />
        <Services messages={messages ? messages : {}} language={language} />
        <ProductCategories
          goPage={goPage}
          messages={messages ? messages : {}}
          language={language}
        />
        <EqualizationHowItWorks
          messages={messages ? messages : {}}
          language={language}
        />
        <LinkContact messages={messages ? messages : {}} language={language} />
      </>
    );
  }
}

const mapStateToProps = (state: any) => ({
  messages: getMessageAppSelector(state),
  language: getLanguageSelector(state),
});

function mapDispatchToProps(dispatch: any) {
  return bindActionCreators(
    {
      goPage: goPageAction,
    },
    dispatch,
  );
}

export default connect(mapStateToProps, mapDispatchToProps)(HomeContainer);
