import { ajax } from "rxjs/ajax";
import { map, catchError } from "rxjs/operators";
import { Observable } from "rxjs";

export interface IProductsService {
  get: (index: number, rows: number) => Observable<Response>;
}

export class ProductsServiceApi implements IProductsService {
  url = "https://api-sepomex.hckdrk.mx/query";
  get(index: number, rows: number) {
    return ajax.get(this.url + "/get_estados").pipe(
      map((response) => {
        //subscriber.next(response);
        console.log("tengo respuesta del servicio:::");
        const estados: any = [];
        if (
          response &&
          response.response &&
          response.response.response &&
          response.response.response.estado
        ) {
          response.response.response.estado.map((es: any) => {
            console.log(es);
            estados.push(es);
          });
        }
        return estados;
      }),
      catchError((er) => {
        console.log("entra al error");
        console.log("error: ", er.response);
        throw er.response.data;
      }),
    );
  }
}
