/* eslint-disable @typescript-eslint/no-empty-interface */
import { combineEpics, Epic } from "redux-observable";
import { languageAction } from "./actions";
import { isOfType } from "typesafe-actions";
import { of } from "rxjs";
import { map, catchError, mergeMap, filter } from "rxjs/operators";
import { goPage, backPage } from "../../services/route";
import { UtilAction, UtilActionTypes } from "./actions";
import { IUtilState, IUtilReducer } from "./reducer";
import { Action } from "redux";
import { IState } from "../reducer";

export interface IUtilT<UtilAction, IUtilReducer> {}

const goEpic: Epic<UtilAction, UtilAction, IState> = (
  action$,
  state$,
  { serviceApi },
) =>
  action$.pipe(
    filter(isOfType(UtilActionTypes.GO_PAGE)),
    map((action) => {
      console.log("UtilActionTypes.GO_PAGE");
      console.log(JSON.stringify(action.payload.path));
      return action.payload.path;
    }),
    map((path) => {
      console.log("tengo respuesta... UtilActionTypes.GO_PAGE");
      console.log(path);
      return goPage(path);
    }),
    catchError((e) => {
      languageAction("");
      return of(e);
    }),
  );

const backEpic: Epic<UtilAction, UtilAction, IUtilState> = (
  action$,
  state$,
  { serviceApi },
) =>
  action$.pipe(
    filter(isOfType(UtilActionTypes.BACK_PAGE)),
    map((action) => {
      return backPage();
    }),
    catchError((e) => {
      languageAction("");
      return of(e);
    }),
  );
export default combineEpics(goEpic, backEpic);
