/* eslint-disable @typescript-eslint/no-this-alias */
import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { Container, LinearProgress } from "@material-ui/core";
import { connect } from "react-redux";
import {
  getLoadingSelector,
  getMessageAppSelector,
  getLanguageSelector,
} from "../../../store/util/selectors";
import { languageAction } from "../../../store/util/actions";
import SearchAppBar from "../../organisms/SearchAppBar";
import AppFooter from "../../organisms/AppFooter";

interface IProps {
  children: any;
  loading: boolean;
  messages: any;
  language: any;
  setLanguage: any;
}

class Template extends Component<IProps> {
  changeLanguage = (id: any) => {
    this.props.setLanguage(id);
  };
  render() {
    const { children, loading, messages, language } = this.props;
    return (
      <>
        <SearchAppBar messages={messages ? messages : {}} language={language} />
        {loading ? <LinearProgress /> : null}
        <>{children}</>
        <AppFooter
          messages={messages ? messages : {}}
          language={language}
          changeLanguage={this.changeLanguage}
        />
      </>
    );
  }
}

const mapStateToProps = (state: any) => ({
  loading: getLoadingSelector(state),
  messages: getMessageAppSelector(state),
  language: getLanguageSelector(state),
});

function mapDispatchToProps(dispatch: any) {
  return bindActionCreators(
    {
      setLanguage: languageAction,
    },
    dispatch,
  );
}

export default connect(mapStateToProps, mapDispatchToProps)(Template);
