import {
  IProductsService,
  ProductsServiceApi,
} from "../../services/api/ProductsServiceApi";

export class ApiServicesFactory {
  private productsService: IProductsService | null = null;

  create(): ApiServicesFactory {
    if (!this.productsService) {
      this.productsService = new ProductsServiceApi();
    }
    return this;
  }
}
