import React from "react";
import { Route, Switch } from "react-router-dom";
import { createBrowserHistory } from "history";
import { ConnectedRouter } from "connected-react-router";
import HomePage from "../../components/pages/HomePage";
import AddProductPage from "../../components/pages/AddProductPage";
import ProductsPage from "../../components/pages/ProductsPage";
import { push } from "connected-react-router";

const AppRoute = (props: any) => {
  return (
    <ConnectedRouter history={props.history}>
      <Switch>
        <Route exact path="/Master_Ultracolor/" children={<HomePage />} />
        <Route
          exact
          path="/Master_Ultracolor/plastic"
          children={<ProductsPage type="plastic" />}
        />
        <Route
          exact
          path="/Master_Ultracolor/masterbatch"
          children={<ProductsPage type="masterbatch" />}
        />
        <Route
          exact
          path="/Master_Ultracolor/pigments"
          children={<ProductsPage type="pigments" />}
        />
        <Route
          exact
          path="/Master_Ultracolor/equalization"
          children={<ProductsPage type="equalization" />}
        />
        <Route
          exact
          path="/Master_Ultracolor/other"
          cchildren={<ProductsPage type="other" />}
        />
        <Route
          exact
          path="/Master_Ultracolor/addProduct"
          children={<AddProductPage />}
        />
      </Switch>
    </ConnectedRouter>
  );
};

export default AppRoute;
export const goPage = (path: any) => push(path);
export const backPage = () => console.log;
