class ProductsContainer extends Component<IProps, IState> {
  constructor(props: IProps | Readonly<IProps>) {
    super(props);
    this.state = {
      loading: true,
    };
    console.log("ProductsContainer");
  }

  componentDidMount() {
    const _this_ = this;
    setTimeout(function () {
      _this_.setState({ loading: false });
    }, 1000);
  }

  product = (id: any) => {
    console.log("go");
    console.log("id");
  };

  getProduct = (id: any) => {
    if (id === "plastic") {
      return "categories.plastic";
    } else if (id === "masterbatch") {
      return "categories.master";
    } else if (id === "pigments") {
      return "categories.pigments";
    } else if (id === "equalization") {
      return "categories.equalization";
    } else {
      return "categories.outro";
    }
  };

  render() {
    const { type, messages, language } = this.props;
    const { loading } = this.state;
    return (
      <>
        <Products
          data={data}
          product={this.getProduct(type)}
          goProduct={this.product}
          messages={messages ? messages : {}}
          language={language}
        />
      </>
    );
  }
}

const mapStateToProps = (state: any) => ({
  messages: getMessageAppSelector(state),
  language: getLanguageSelector(state),
});

function mapDispatchToProps(dispatch: any) {
  return bindActionCreators({}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductsContainer);
