import React, { Component } from "react";
import { FormattedMessage } from "react-intl";
import { messages, languageNav } from "../../../App";

/* eslint no-eval: 0 */
/* eslint no-unused-vars: 0 */
/* eslint valid-typeof: 0 */
class Util {
  static getFormattedMessage(id: any) {
    return <FormattedMessage id={id} />;
  }
  static getMessage(intl: any, label: any) {
    return intl.formatMessage({ id: label });
  }

  static traducedMessage(stateValue: any, id: string) {
    // console.log("messages::");
    // console.log(JSON.stringify(stateValue));
    // console.log("traduc--->", stateValue[id]);
    if (stateValue && stateValue[id]) {
      return stateValue[id];
    } else {
      if (messages[languageNav][id]) {
        return messages[languageNav][id];
      } else {
        return id;
      }
    }
  }
}
export default Util;
