export const messages: any = {
  es: messagesEs,
  en: messagesEn,
  fr: messagesFr,
};

export const languageNav: any = navigator.language.split(/[-_]/)[0]; // language without region code

class Store implements Object {
  dispatch: any;
}

interface IProps {
  data?: any;
  behaviorTemplate?: any;
  theme?: any;
  language?: string;
  agent?: string;
  eventReceive?: (action: any) => void | null;
  isTestWeb?: boolean;
}

const store = ConfigureStore();
class App extends React.Component<IProps> {
  constructor(props: IProps | Readonly<IProps>) {
    super(props);
    //history.push("/launch/appid")
    store.dispatch({
      type: UtilActionTypes.LANGUAGE,
      payload: {
        language: this.props.language ? this.props.language : languageNav,
      },
    });
    store.dispatch({
      type: UtilActionTypes.MESSAGES,
      payload: {
        messagesApp: messages,
      },
    });
  }

  componentDidCatch(error: any, info: any) {
    console.log(error, info);
  }

  render() {
    return (
      <Provider store={store}>
        <Components
          language={languageNav}
          messages={messages}
          history={history}
        />
      </Provider>
    );
  }
}

export default App;
