import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { Fade, Modal, Grid, Button } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  modalContent: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[7],
    minHeight: 400,
    margin: 10,
    padding: theme.spacing(2, 4, 3),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "space-between",
  },
}));
import { ChromePicker } from "react-color";
export const ColorPicker = (props: any) => {
  const classes = useStyles();
  const [color, setColor] = useState(props.currentColor);
  const [showPicker, setShowPicker] = useState(false);
  const handleChange = (color: any) => {
    setColor(color.hex);
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        alignItems: "center",
        padding: 10,
      }}
    >
      <label>{props.label}</label>
      <div
        style={{
          marginLeft: 20,
          marginRight: 20,
          padding: 15,
          paddingLeft: 20,
          paddingRight: 20,
          borderRadius: 3,
          backgroundColor: color,
        }}
        onClick={() => setShowPicker(true)}
      ></div>
      {props.moreIcon}
      {showPicker && (
        <Modal
          className={classes.modal}
          open={showPicker}
          closeAfterTransition
          BackdropProps={{
            timeout: 500,
          }}
        >
          <Fade in={true}>
            <div className={classes.modalContent}>
              <label>{props.label}</label>
              <div
                style={{
                  backgroundColor: color,
                  marginTop: 10,
                  marginBottom: 10,
                  padding: 40,
                  borderRadius: 3,
                }}
              >
                <ChromePicker color={color} onChange={handleChange} />
              </div>
              <Grid container spacing={10} justify="space-evenly">
                <Grid
                  item
                  xs={12}
                  md={6}
                  style={{ flexBasis: "auto", padding: 30 }}
                >
                  <Button
                    color="inherit"
                    onClick={() => {
                      setShowPicker(false);
                    }}
                  >
                    {"ACTIONS.CANCEL"}
                  </Button>
                </Grid>
                <Grid
                  item
                  xs={12}
                  md={6}
                  style={{ flexBasis: "auto", padding: 30 }}
                >
                  <Button
                    color="inherit"
                    type="submit"
                    onClick={() => {
                      props.onChange(color, props.id);
                      setShowPicker(false);
                    }}
                  >
                    {"ACTIONS.SAVE"}
                  </Button>
                </Grid>
              </Grid>
            </div>
          </Fade>
        </Modal>
      )}
    </div>
  );
};
