import produce from "immer";
import { UtilAction, UtilActionTypes } from "./actions";

export interface IUtilReducer {
  (state: IUtilState, action: UtilAction): Record<string, any>;
}

export interface IUtilState {
  theme: any;
  language: any;
  agent: any;
  loading: boolean;
  messagesApp: any;
}

export const initialUtilState: IUtilState = {
  theme: null,
  language: null,
  agent: null,
  loading: false,
  messagesApp: null,
};

export default function utilReducer(
  state: IUtilState = initialUtilState,
  action: UtilAction,
) {
  return produce(state, (draft) => {
    switch (action.type) {
      case UtilActionTypes.ACTION:
        break;

      case UtilActionTypes.THEME:
        draft.theme = action.payload.theme;
        break;

      case UtilActionTypes.LANGUAGE:
        draft.language = action.payload.language;
        break;

      case UtilActionTypes.AGENT:
        draft.agent = action.payload.agent;
        break;

      case UtilActionTypes.LOADING:
        draft.loading = action.payload.loading;
        break;

      case UtilActionTypes.MESSAGES:
        draft.messagesApp = action.payload.messagesApp;
        break;
    }
  });
}
